/*--------------------------------------------------------------
# Buttons
--------------------------------------------------------------*/
.button {
  display: inline-flex;
  font-size: 14px;
  line-height: 22px;
  font-weight: 600;
  padding: 12px 31px;
  height: 48px;
  text-decoration: none !important;
  text-transform: uppercase;
  color: #4A61DD;
  background-color: rgba(255, 255, 255, 0.925);
  border-width: 1px;
  border-style: solid;
  border-color: transparent;
  border-radius: 15px;
  cursor: pointer;
  justify-content: center;
  text-align: center;
  letter-spacing: inherit;
  white-space: nowrap;
  transition: background 0.15s ease;
}
.button:active {
  outline: 0;
}
.button:hover {
  background-color: rgba(255, 255, 255, 0.925);
}
.button.is-loading {
  position: relative;
  color: transparent !important;
  pointer-events: none;
}
.button.is-loading::after {
  content: "";
  display: block;
  position: absolute;
  width: 22px;
  height: 22px;
  margin-left: -11px;
  margin-top: -11px;
  top: 50%;
  left: 50%;
  border: 2px solid #4A61DD;
  border-radius: 50%;
  border-right-color: transparent !important;
  border-top-color: transparent !important;
  z-index: 1;
  animation: button-loading 0.6s infinite linear;
}
.button[disabled] {
  cursor: not-allowed;
  color: #2A2C36;
  background-color: #3F455A !important;
}
.button[disabled].is-loading::after {
  border-color: #2A2C36;
}

.button-sm {
  padding: 6px 23px;
  height: 36px;
}

.button-dark {
  color: rgba(255, 255, 255, 0.925);
  background-color: #2A2C36;
}
.button-dark:hover {
  background-color: #31333f;
}
.button-dark.is-loading::after {
  border-color: rgba(255, 255, 255, 0.925);
}

.button-primary {
  color: rgba(255, 255, 255, 0.925);
  background-color: #4A61DD;
}
.button-primary:hover {
  background-color: #576cdf;
}
.button-primary.is-loading::after {
  border-color: rgba(255, 255, 255, 0.925);
}

.button-secondary {
