}
.button-secondary:hover {
  background-color: #3bade4;
}
.button-secondary.is-loading::after {
  border-color: rgba(255, 255, 255, 0.925);
}

.button-block {
  display: flex;
  width: 100%;
}

.button-group {
  display: flex;
